
/******************************navbar******************/

@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap');

:root {
  --primary-white: #fff;
  --primay-orange: #f90;
  --primary-dark:#000;
  --primary-red :#eb400c;
}

body {
  font-family: 'Roboto Condensed', sans-serif;
}

.logo {
  width: 40px;

}

nav a.nav-link {
  letter-spacing: 0.4px;
  cursor: pointer;
  
}
nav li ul {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  float: left;
  display: block;
  margin-right: 10px;
  margin-left: 10px;
  border-top: 3px  var(--primary-dark);
}
.navbar-brand {
  max-width: 40px;
  padding: 0 !important;
  height: 25px !important;
}
.navbar-nav > li > a.active {

  border-top: 3px solid var(--primay-orange);
}
.navbar-nav > li > a::after {
  content: "";
  position: relative;
  display: block;
  height: 5px;
  width: 0;
  border-radius: 16px;
  background: #c95bf5;
  bottom: 1px;
  left: 0;
  z-index: -1;
  transition: all 0.3s ease-out 0s;

}
.bg-dark {
  background-color: #5183b6!important;
}
.navbar {
  position: fixed !important;
  transition: all 0.3s ease-out 0s !important;
  padding: 0.3rem 2rem !important;
  font-size: 1.2rem !important;
}


